import React, { useState } from 'react';
import axios from "axios";
import { useLocation, useNavigate } from 'react-router-dom';
import Paper from "@mui/material/Paper";
import Box from "@mui/material/Box";
import Typography from "@mui/material/Typography";
import TextField from "@mui/material/TextField";
import Button from "@mui/material/Button";

const IssueCreate = () => {
    const location = useLocation();
    const [issueTitle, setIssueTitle] = useState('');
    const [issueDescription, setIssueDescription] = useState('');
    const [issueAssignee, setAssignee] = useState('');
    const { currentProject } = location.state || {};
    const navigate = useNavigate();

    const handleCreate = () => {
        axios.post(`/api/projects/${currentProject.id}/issues/create`, {
            issueTitle: issueTitle,
            issueDescription: issueDescription
        })
            .then((response) => {
                return axios.post(`/api/projects/${currentProject.id}/issues/${response.data.id}/update-dev`, {
                    assignee: issueAssignee
                });
            })
            .then(() => {
                alert('  .');
                navigate(`/Project/${currentProject.projectTitle}`);
            })
            .catch((error) => {
                console.log(error);
                alert('  .');
            });
    };

    return (
        <Paper sx={{ width: '90%', margin: 'auto', padding: 4 }}>
            <Box sx={{ display: 'flex', flexDirection: 'column', alignItems: 'center', mt: 4 }}>
                <Typography variant="h5" sx={{ mb: 2, fontWeight: 'bold' }}> </Typography>
                <TextField
                    label=""
                    value={issueTitle}
                    onChange={(e) => setIssueTitle(e.target.value)}
                    variant="outlined"
                    margin="normal"
                    fullWidth
                    required
                    color="success"
                    sx={{
                        '& .MuiOutlinedInput-root': {
                            '&.Mui-focused fieldset': {
                                borderColor: '#03C75A', //    
                            },
                        },
                    }}
                />
                <TextField
                    label=""
                    value={issueDescription}
                    onChange={(e) => setIssueDescription(e.target.value)}
                    variant="outlined"
                    margin="normal"
                    fullWidth
                    multiline
                    required
                    rows={4}
                    color="success"
                    sx={{
                        '& .MuiOutlinedInput-root': {
                            '&.Mui-focused fieldset': {
                                borderColor: '#03C75A', //    
                            },
                        },
                    }}
                />
                <TextField
                    label=""
                    value={issueAssignee}
                    onChange={(e) => setAssignee(e.target.value)}
                    margin="normal"
                    color="success"
                    sx={{
                        '& .MuiOutlinedInput-root': {
                            '&.Mui-focused fieldset': {
                                borderColor: '#03C75A', //    
                            },
                            '& .MuiInputLabel-root': {
                                color: '#03C75A', //   
                            },
                        },
                    }}
                />
                <Button variant="contained" color="primary" onClick={handleCreate} sx={{ mt: 3,
                    backgroundColor: '#03C75A', //  
                    '&:hover': {
                        backgroundColor: '#03C75A', //   
                    },
                }}>
                    
                </Button>
            </Box>
        </Paper>
    );
};

export default IssueCreate;
