import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import TextField from '@mui/material/TextField';
import Button from '@mui/material/Button';
import Box from '@mui/material/Box';
import Paper from '@mui/material/Paper';
import Typography from '@mui/material/Typography';
import axios from "axios";
import {Autocomplete, Chip} from "@mui/material";

export default function ProjectCreate(){
    const [projectTitle, setProjectTitle] = useState('');
    const [projectDescription, setProjectDescription] = useState('');
    const [plUser, setPlUser] = useState('');
    const [devUser, setDevUser] = useState('');
    const [testUser, setTestUser] = useState('');
    const navigate = useNavigate();

    const handleCreate = () => {
        axios.post('/api/projects/create', {
            projectTitle: projectTitle,
            projectDescription: projectDescription,
            plUser: plUser,
            devUser: devUser,
            testUser: testUser
        })
            .then((response) => {
                console.log(response);
                alert('  .');
            })
            .catch(function (error) {
                console.log(error);
                alert('  .');
            });
        navigate(0);
        navigate('/Project');
    };

    return (
        <Paper sx={{ width: '90%', margin: 'auto', padding: 4 }}>
            <Box sx={{ display: 'flex', flexDirection: 'column', alignItems: 'center', mt: 4 }}>
                <Typography variant="h5" sx={{ mb: 2, fontWeight: 'bold' }}> </Typography>
                <TextField
                    label=""
                    value={projectTitle}
                    onChange={(e) => setProjectTitle(e.target.value)}
                    variant="outlined"
                    margin="normal"
                    fullWidth
                    required
                    color="success"
                    sx={{
                        '& .MuiOutlinedInput-root': {
                            '&.Mui-focused fieldset': {
                                borderColor: '#03C75A', //    
                            },
                        },
                    }}
                />
                <TextField
                    label=""
                    value={projectDescription}
                    onChange={(e) => setProjectDescription(e.target.value)}
                    variant="outlined"
                    margin="normal"
                    fullWidth
                    multiline
                    required
                    rows={4}
                    color="success"
                    sx={{
                        '& .MuiOutlinedInput-root': {
                            '&.Mui-focused fieldset': {
                                borderColor: '#03C75A', //    
                            },
                        },
                    }}
                />
                <Autocomplete
                multiple
                id="tags-filled"
                options={[]}
                fullWidth
                required
                value={plUser}
                onChange={(event, newValue) => {
                    setPlUser(newValue);
                }}
                freeSolo
                renderTags={(value, getTagProps) =>
                    value.map((option, index) => (
                        <Chip variant="outlined" label={option} {...getTagProps({ index })} />
                    ))
                }
                renderInput={(params) => (
                    <TextField
                        {...params}
                        color="success"
                        variant="outlined"
                        sx={{
                            '& .MuiOutlinedInput-root': {
                                '&.Mui-focused fieldset': {
                                    borderColor: '#03C75A', //    
                                },
                            },
                        }}
                        label="PL"
                        required
                        margin="normal"
                    />
                )}
            />
                <Autocomplete
                    multiple
                    id="tags-filled"
                    options={[]}
                    fullWidth
                    required
                    value={devUser}
                    onChange={(event, newValue) => {
                        setDevUser(newValue);
                    }}
                    freeSolo
                    renderTags={(value, getTagProps) =>
                        value.map((option, index) => (
                            <Chip variant="outlined" label={option} {...getTagProps({ index })} />
                        ))
                    }
                    renderInput={(params) => (
                        <TextField
                            {...params}
                            color="success"
                            variant="outlined"
                            sx={{
                                '& .MuiOutlinedInput-root': {
                                    '&.Mui-focused fieldset': {
                                        borderColor: '#03C75A', //    
                                    },
                                },
                            }}
                            label="Dev"
                            required
                            margin="normal"
                        />
                    )}
                />
                <Autocomplete
                    multiple
                    id="tags-filled"
                    options={[]}
                    fullWidth
                    required
                    value={testUser}
                    onChange={(event, newValue) => {
                        setTestUser(newValue);
                    }}
                    freeSolo
                    renderTags={(value, getTagProps) =>
                        value.map((option, index) => (
                            <Chip variant="outlined" label={option} {...getTagProps({ index })} />
                        ))
                    }
                    renderInput={(params) => (
                        <TextField
                            {...params}
                            color="success"
                            variant="outlined"
                            sx={{
                                '& .MuiOutlinedInput-root': {
                                    '&.Mui-focused fieldset': {
                                        borderColor: '#03C75A', //    
                                    },
                                },
                            }}
                            label="Tester"
                            required
                            margin="normal"
                        />
                    )}
                />
                <Button variant="contained" color="primary" onClick={handleCreate} sx={{ mt: 3,
                    backgroundColor: '#03C75A', //  
                    '&:hover': {
                        backgroundColor: '#03C75A', //   
                    },
                }}>
                    
                </Button>
            </Box>
        </Paper>
    );
};
